<template>
  <div id="divextern">
    <div class="divexternraum">
      <p class="titel" :class="{darkmodeE : isDarkmode}">{{ raumname }} ( {{ getAnzahlPat }} Pat.)</p>
      <div id="extern">
        <table class="tablestyle">
          <tr>
            <th></th>
            <th>Name</th>
            <th>OE</th>
            <th>M.</th>
            <th>Ext.</th>
          </tr>
          <tr v-for="(person, i) in persons" v-bind:key="i">
            <td :class="getAusrufezeichen(`${person.warnung}`)"></td>
            <td style="font-size: 10px;">{{ person.name }}</td>
            <td style="font-size: 10px;" class="tooltip">
              {{ person.fachOE }}
              <span class="tooltiptext">{{ person.diagnose }}</span>
            </td>
            <td
              style="font-size: 10px;"
              :class="getwartezeit(person.wartezeit, person.triage)"
            >
              <span>{{ person.wartezeit }}</span>
            </td>
            <td class="tooltip">
              {{ person.externort }}
              <span class="tooltiptext">{{ person.externorttext }}</span>
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isClicked: false
    };
  },
  props: ["raumname", "persons"],
  computed: {
    getAnzahlPat() {
      return this.persons.length;
    },
    isDarkmode() {
      return this.$store.getters["isDarkmode"];
    }
  },
  methods: {
    toggleIsClicked() {
      this.$store.commit("toggleDarkmode");
    },
    getwartezeit(wartezeit, triage) {
      wartezeit = wartezeit.replace("Min", "").trim();
      console.log("getwartezeit");
      if ((triage == 1) & (wartezeit > 0)) {
        return "redWartezeit";
      } else if ((triage == 2) & (wartezeit > 10)) {
        return "redWartezeit";
      } else if ((triage == 3) & (wartezeit > 30)) {
        return "redWartezeit";
      } else if ((triage == 4) & (wartezeit >= 90)) {
        return "redWartezeit";
      } else if ((triage == 5) & (wartezeit >= 120)) {
        return "redWartezeit";
      } else if (wartezeit > 90) {
        return "redWartezeit";
      } else {
        return "greenWartezeit";
      }
    },
    getAusrufezeichen(warnung) {
      switch (warnung) {
        case "1RFON":
          return "ausrufezeichen";
        case "1RFOFF":
          return "ausrufezeichen";
        case "0GLPRFOFF":
          return "gelberPunkt";
        case "0GLPRFON":
          return "gelberPunkt";
        case "1GLPRFON":
          return "gelberPunkt";
      }
    }
  }
};
</script>

<style scoped>
.divexternraum {
  display: grid;
  grid-template-rows: repeat(2, [row] auto);
  background-color: #e6e6e6;
  width: auto;
  height: auto;
  margin: 5px 5px;
}

.darkmodeE {
 background-color: #FCBF49 !important;
}

.titel {
  /* border: 2px solid #89bdee;
  background-color: #d5ebff;
  border-radius: 16px;
  font-size: 12px;
  font-family: sans-serif;
  text-align: center;
  margin: 5px;
  width: auto;
  height: 15px;
  justify-content: center; */

  border: 3px solid #ffffff;
  border-radius: 0 0 15px 15px;
  border-style: none solid solid;
  background-color: #d5ebff;
  font-size: 14px;
  font-family: sans-serif;
  text-align: center;
  margin: 0px 15px 10px 15px;
  width: auto;
  height: 15px;
  justify-content: center;
  padding: 5px;
  font-weight: bold;
  color: #444;
}

.tablestyle {
  border: 3px solid #b7bdc4;
  border-radius: 4px;
  font-size: 12px;
  width: 100%;
  height: auto;
}

table th {
  border: 1px solid #b7bdc4;
  text-align: left;
  background-color: #f0f3f4;
  width: 5px;
}

td {
  border: 1px solid #b7bdc4;
  width: 100%;
  background-color: white;
}

table tr:nth-child(even) {
  background-color: #e6e6e6;
}

table tr:nth-child(odd) {
  background-color: white;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  border: 2px solid #521751;
  visibility: hidden;
  background-color: white;
  color: black;
  font-size: 12px;
  text-align: center;
  border-radius: 6px;
  padding: 5px 15px;
  position: absolute;
  z-index: 20;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.redWartezeit {
  background-color: #f08080;
}

.greenWartezeit {
  background-color: #90ee90;
}
</style>
