<template>
  <div id="signin">
    <div class="signin-form">
      <form @submit.prevent="onSubmit">
        <div class="input">
          <h1>
            <img
              src="https://mysites.mh-hannover.local/personal/mh-hannover_dangmarc/Documents/F%C3%BCr%20jeden%20freigegeben/ZNA_Icon_Package/Logo_schwarz_solo.png"
              width="80px"
            />
            <div class="h1dashboard">&nbsp;Dashboard</div>
          </h1>
          <p>Anmelden</p>
          <hr class="one" />
          <hr class="two" />
          <br />
          <center v-if="error" style="color:red">{{ error }}</center>
          <input
            id="username"
            type="text"
            class="form-control"
            required
            v-model="userData.username"
            placeholder="Benutzername"
          />
          <input
            id="password"
            type="password"
            class="form-control"
            required
            v-model="userData.password"
            placeholder="Passwort"
          />
        </div>
        <br />
        <div class="dashboardborder">
          <div class="dashboarddiv">
            <hr />
          </div>
          <div class="dashboarddiv">DASHBOARD AUSWHLEN</div>
          <div class="dashboarddiv">
            <hr />
          </div>
        </div>
        <br />
        <div class="row">
          <div
            class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3 from-group"
          >
            <select
              id="dashboard"
              class="form-control"
              v-model="selectedDashboard"
            >
              <option v-for="(dashboard, i) in dashboards" v-bind:key="i">{{
                dashboard
              }}</option>
            </select>
          </div>
        </div>
        <br />
        <br />
        <div class="submit">
          <button type="submit">Start</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "signUserIn",
  data() {
    return {
      userData: {
        username: "",
        password: ""
      },
      selectedDashboard: "ZNA Dash Prod.",
      dashboards: [
        "ZNA Dash Prod.",
        "ZNA Dash Prod. (anonym)",
        "ZNA Dash Test"
      ],
      /* dataSwitch: true, */
      isSubmitted: false,
      error: ""
    };
  },
  computed: {},
  watch: {},
  methods: {
    onSubmit() {
      var _this = this;
      this.$store.getters["login"](
        this.userData.username,
        this.userData.password,
        this.selectedDashboard
      ).then(
        function() {
          _this.$router.push("/dashboard");
        },
        function() {
          _this.error = "Invalid username/password";
        }
      );
    }
  }
};
</script>

<style scoped>
body {
  height: 100%;
  background: linear-gradient(to bottom, #a9d0f5 40%, white 100%);
  font-family: sans-serif;
  justify-content: center;
  position: relative;
}

.signin-form {
  border-radius: 16px;
  width: 400px;
  margin: 150px auto;
  border: 3px solid #0489b1;
  padding: 30px;
  background-color: white;
  /* box-shadow: 0 2px 3px #ccc; */
}

h1 {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  font-size: 40px;
}

p {
  display: flex;
  justify-content: center;
  margin-top: 1px;
}

hr.one {
  width: 350px;
  height: 2px;
  border-radius: 8px;
  background: #0489b1;
}

hr.two {
  width: 300px;
  height: 1px;
  border-radius: 8px;
  background: #04b486;
}

.input {
  margin: 10px auto;
  overflow: hidden;
}

.input h1 {
  text-align: center;
}

.input label {
  display: block;
  color: #4e4e4e;
  margin-bottom: 6px;
}

.input input {
  font: inherit;
  width: 100%;
  padding: 6px 12px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-style: none none solid none;
  margin: 6px 0 6px 0;
  box-sizing: content-box;
  transition: border 0.1s ease-in-out;
}

.input input:focus {
  outline: none;
  border: 2px solid #0489b1;
  border-style: none none solid none;
  /* background-color: #eee; */
}

.known {
  font-size: 12px;
  margin-left: -290px;
}

.row div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row div select {
  padding: 6px;
  border-radius: 6px;
  font-size: 0.9em;
  border: 1px solid #0489b1;
}

.submit {
  display: flex;
  flex-direction: column;
}

.submit button {
  background-color: #04b486;
  border: 1px solid #04b486;
  color: white;
  font: inherit;
  cursor: pointer;
  transition: background-color 0.05s ease-out;
  border-radius: 6px;
  padding: 5px;
}

.submit button:hover,
.submit button:active {
  background-color: white;
  color: black;
}

.submit button[disabled],
.submit button[disabled]:hover,
.submit button[disabled]:active {
  border: 1px solid #ccc;
  background-color: transparent;
  color: #ccc;
  cursor: not-allowed;
}

.dashboardborder {
  display: flex;
  flex-direction: row-reverse;
  margin-left: 25px;
  margin-right: 25px;
}

.dashboarddiv {
  width: 150px;
  font-size: 8px;
  color: gray;
  margin: 5px;
}

@media only screen and (orientation: portrait) {
