import Vue from 'vue'
import Vuex from 'vuex'
import dashboard_utils from './utils.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username: null,
    password: null,
    dashboard: null,
    darkmode: false
  },
  getters: {
    username: (state) => {
      return state.username;
    },
    password: (state) => {
      return state.password;
    },
    dashboard: (state) => {
      return state.dashboard;
    },
    isAnonym: (state) => {
      return state.dashboard == "ZNA Dash Prod. (anonym)";
    },
    isDarkmode:(state) => {
      return state.darkmode;
    },
    login: (state) => (username, password, dashboard) => {
      return new Promise(function(resolve, reject) 
      {
        state.username = username;
        state.password = password;
        state.dashboard = dashboard;

        dashboard_utils.getXML().then(
          function() { resolve(true) },
          function() { 
            state.username = null;
            state.password = null;
            state.dashboard = null;
            reject(false);
           }
        );
      });      
    }
  },
  actions: {
  },
  mutations: {
    toggleDarkmode(state) {
      state.darkmode = !state.darkmode
    } 
  }

})
