<template>
  <div id="tabellen">
    <div class="tabellen">
      <p class="titel" :class="{darkmodeT : isDarkmode}">{{ raumname }} ( {{ getAnzahlPat }} Pat.)</p>
      <div id="tabellewz">
        <table class="tablestyle">
          <tr class="color1">
            <th></th>
            <th>Name</th>
            <th>OE</th>
            <th>M.</th>
            <th>b.</th>
            <th>L.</th>
            <th>R.</th>
            <th>T.</th>
          </tr>
          <tr v-for="(person, i) in persons" v-bind:key="i">
            <td
              style="width: 15px;"
              :class="getAusrufezeichen(`${person.warnung}`)"
            ></td>
            <td :class="getfachoe(`${person.fachOE}`)" style="margin: 1px 1px;">
              {{ person.name }}
              <!-- <span class="tooltiptext">{{ person.name }}</span> -->
            </td>
            <td class="tooltip">
              {{ person.fachOE }}
              <span class="tooltiptext">{{ person.diagnose }}</span>
            </td>
            <td style="font-size: 12px;">
              <span :class="getwartezeit(person.colWartezeit)">{{
                person.wartezeit
              }}</span>
            </td>
            <td :class="getbehandelt(`${person.behandelt}`)">
              {{ person.behandelt }}
            </td>
            <td
              :class="[getlabor(person.labor), getBlink(person.laborwarnung)]"
            >
              {{ person.labor }}
            </td>
            <td :class="getroentgen(`${person.roentgen}`)">
              {{ person.roentgen }}
            </td>
            <td style="width: 25px;" :class="gettriage(`${person.triage}`)">
              {{ person.triage }}
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isClicked: false
    };
  },
  props: ["raumname", "persons"],
  computed: {
    getAnzahlPat() {
      return this.persons.length;
    },
    isDarkmode() {
      return this.$store.getters["isDarkmode"];
    }
  },
  methods: {
    toggleIsClicked() {
      this.$store.commit("toggleDarkmode");
    },
    getAusrufezeichen(warnung) {
      switch (warnung) {
        case "1RFON":
          return "ausrufezeichen";
        case "1RFOFF":
          return "ausrufezeichen";
        case "0GLPRFOFF":
          return "gelberPunkt";
        case "0GLPRFON":
          return "gelberPunkt";
        case "1GLPRFON":
          return "gelberPunkt";
      }
    },
    getfachoe(fachOE) {
      switch (fachOE) {
        case "NEU":
          return "fachgruen";
        case "INN-ZE":
          return "fachrot";
        case "NCH":
          return "fachbraun";
        case "PSK":
          return "fachlila";
        case "UCH":
          return "fachblau";
        case "PHW":
          return "fachorange";
        case "ALL":
          return "fachgrau";
        case "HNO":
          return "fachhellgrau";
        case "AUG":
          return "fachhellgrau";
        case "THG":
          return "fachhellgrau";
        case "DER":
          return "fachgrau";
        case "ZCH":
          return "fachgrau";
        case "MED":
          return "fachrot";
        case "AII":
          return "fachrot";
        case "GAS":
          return "fachrot";
        case "HAE":
          return "fachrot";
        case "IFK":
          return "fachrot";
        case "KA1":
          return "fachrot";
        case "KAR":
          return "fachrot";
        case "NPH":
          return "fachrot";
        case "PNE":
          return "fachrot";
        case "RHT":
          return "fachrot";
        case "URO":
          return "fachgelb";
        case "HTTG":
          return "fachblack";
        case "ACH":
          return "fachpink";
        case "ZNK":
          return "fachgelb";
        default:
          return "fachgrau";
      }
    },
    getwartezeit(colWartezeit) {
      switch (colWartezeit) {
        case "Red":
          return "redWartezeit";
        case "Green":
          return "greenWartezeit";
      }
    },
    /* getwartezeit(wartezeit, triage) {
      wartezeit = wartezeit.replace("Min", "").trim();
      console.log("getwartezeit");
      if ((triage == 1) & (wartezeit > 0)) {
        return "redWartezeit";
      } else if ((triage == 2) & (wartezeit > 10)) {
        return "redWartezeit";
      } else if ((triage == 3) & (wartezeit > 30)) {
        return "redWartezeit";
      } else if ((triage == 4) & (wartezeit >= 90)) {
        return "redWartezeit";
      } else if ((triage == 5) & (wartezeit >= 120)) {
        return "redWartezeit";
      } else if (wartezeit > 90) {
        return "redWartezeit";
      } else {
        return "greenWartezeit";
      }
    } */
    getbehandelt(behandelt) {
      switch (behandelt) {
        case "0":
          return "null";
        case "1":
          return "second";
        case "2":
          return "second";
      }
    },
    getlabor(labor) {
      switch (labor) {
        case "0":
          return "null";
        case "1":
          return "first";
        case "2":
          return "second";
      }
    },
    getBlink(labor) {
      switch (labor) {
        case "1":
          return "blinking";
      }
    },
    getroentgen(roentgen) {
      switch (roentgen) {
        case "0":
          return "null";
        case "1":
          return "first";
        case "2":
          return "second";
      }
    },
    gettriage(triage) {
      switch (triage) {
        case "1":
          return "red";
        case "2":
          return "orange";
        case "3":
          return "yellow";
        case "4":
          return "green";
        case "5":
          return "blue";
        default:
          return;
      }
    }
  },
  mounted: function() {}
};
</script>

