<script>
import DivRaumUBVue from "../divs/DivRaumUB.vue";
import DivTabellenVue from "../divs/DivTabellen.vue";
import DivLaborwarnungVue from "../divs/DivLaborwarnung.vue";
import DivExternVue from "../divs/DivExtern.vue";
import dashboard_utils from "../../utils.js";

export default {
  components: {
    "app-div-raumub": DivRaumUBVue, // Compoment for UB rooms
    "app-div-tabellen": DivTabellenVue, // Compoment for tables
    "app-div-extern": DivExternVue,
    "app-div-laborwarnung": DivLaborwarnungVue
  },
  data() {
    return {
      count: 60, // Timer for reload
      lastDataTime: null, // Last update date from the xml
      persons: [], // Stores all persons form the xml
      selectedDashboard: null,
      isClicked: false
    };
  },
  computed: {
    isDarkmode() {
      return this.$store.getters["isDarkmode"];
    },
    personenB6() {
      return this.persons.filter(person => person.raum_id === "ZNAR0007");
    },
    personenB5() {
      return this.persons.filter(person => person.raum_id === "ZNAR0006");
    },
    personenB4() {
      return this.persons.filter(person => person.raum_id === "ZNAR0005");
    },
    personenB3() {
      return this.persons.filter(person => person.raum_id === "ZNAR0004");
    },
    personenB2() {
      return this.persons.filter(person => person.raum_id === "ZNAR0003");
    },
    personenB1() {
      return this.persons.filter(person => person.raum_id === "ZNAR0002");
    },
    personenBUCH() {
      return this.persons.filter(person => person.raum_id === "ZNAR0029");
    },
    personenGipsraum() {
      return this.persons.filter(person => person.raum_id === "ZNAR0014");
    },
    personenCBogen() {
      return this.persons.filter(person => person.raum_id === "ZNAR0009");
    },
    personenB7() {
      return this.persons.filter(person => person.raum_id === "ZNAR0008");
    },
    personenB9() {
      return this.persons.filter(person => person.raum_id === "ZNAR0011");
    },
    personenWV() {
      return this.persons.filter(person => person.raum_id === "ZNAR0012");
    },
    personenB8() {
      return this.persons.filter(person => person.raum_id === "ZNAR0010");
    },
    personenZVK() {
      return this.persons.filter(person => person.raum_id === "ZNAR0013");
    },
    personenSchockr1() {
      return this.persons.filter(person => person.raum_id === "ZNAR0016");
    },
    personenSchockr2() {
      return this.persons.filter(person => person.raum_id === "ZNAR0017");
    },
    personenISO() {
      return this.persons.filter(person => person.raum_id === "ZNAR0032");
    },
    personenALL() {
      return this.persons.filter(person => person.raum_id === "ZNAR0033");
    },
    personenFlurUB() {
      return this.persons.filter(person => person.raum_id === "ZNAR0026");
    },
    personenRoentgen() {
      return this.persons.filter(person => person.raum_id === "ZNAR0015");
    },
    personenFlurRoentgen() {
      return this.persons.filter(person => person.raum_id === "ZNAR0028");
    },
    personenWZ() {
      return this.persons.filter(person => person.raum_id === "ZNAR0001");
    },
    personenWZTunnel() {
      return this.persons.filter(person => person.raum_id === "ZNAR0024");
    },
    personenISO3() {
      return this.persons.filter(person => person.raum_id === "ZNAR0034");
    },
    personenISO2() {
      return this.persons.filter(person => person.raum_id === "ZNAR0031");
    },
    personenISO1() {
      return this.persons.filter(person => person.raum_id === "ZNAR0030");
    },
    personenKonsile() {
      return this.persons.filter(person => person.raum_id === "ID_ZNA_Konsil");
    },
    personenExtern() {
      return this.persons.filter(person => person.raum_id === "ZNAR0025");
    },
    personenLaborwarnung() {
      return this.persons.filter(person => person.laborwarnung == 1);
    }
  },
  mounted() {
    // Load Data on mounted
    this.getXML();
    // Set Timer
    this.handle = setInterval(() => {
      this.count--;
      if (this.count < 0) {
        this.getXML();
        this.count = 60;
      }
    }, 1000);
  },
  methods: {
    getXML: function() {
      var _this = this;
      dashboard_utils.getXML().then(
        function(value) {
          _this.parseXML(value);
        },
        function(error) {
          console.log("ERROR:", error);
        }
      );
    },
    parseXML: function(xml_response) {
      var anonym = this.$store.getters["isAnonym"];
      if (
        typeof xml_response !== "object" ||
        xml_response.children.length !== 1
      ) {
        alert("Input Error!");
        return;
      }
      // Clear all persons
      this.persons.length = 0;

      // Get 'daten' Field
      var daten = xml_response.children[0];

      // Set last update date
      this.lastDataTime = daten.getAttribute("Daten_Zeit");

      // First looping all Rooms
      for (var room_idx in daten.children) {
        var room = daten.children[room_idx];

        for (var pat_idx in room.children) {
          var pat = room.children[pat_idx];

          if (typeof pat !== "object") continue;

          var person = {
            raum_id: room.getAttribute("Raum_ID"),
            raumbezeichnung: room.getAttribute("Raumbez"),
            name: anonym ? "xxx" : pat.getAttribute("Pat_Name"),
            geschlecht: anonym ? "xxx" : pat.getAttribute("Geschlecht"),
            alter: anonym ? "xxx" : pat.getAttribute("Alter"),
            fachOE: pat.getAttribute("fachOE"),
            wartezeit: anonym ? "xxx" : pat.getAttribute("Wartezeit"),
            diagnose: anonym ? "xxx" : pat.getAttribute("Diagnose"),
            triage: pat.getAttribute("Triage"),
            behandelt: pat.getAttribute("behandelt"),
            labor: pat.getAttribute("Labor"),
            roentgen: pat.getAttribute("Roentgen"),
            laborwarnung: pat.getAttribute("Lab_Warnung"),
            warnung: pat.getAttribute("Warnung"),
            externort: pat.getAttribute("Extern_Ort"),
            externorttext: anonym ? "xxx" : pat.getAttribute("Extern_Ort_Text"),
            konsile: pat.getAttribute("Konsil"),
            colWartezeit: pat.getAttribute("Col_Wartezeit")
          };

          this.persons.push(person);
        }
      }
    },
    startseite() {
      console.log("Click on startseite!");
    },
    /* Get the documentElement (<html>) to display the page in fullscreen */
    openFullscreen() {
      var elem = document.documentElement;
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.webkitRequestFullscreen) {
        /* Safari */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        /* IE11 */
        elem.msRequestFullscreen();
      }
    },
    /* Close fullscreen */
    closeFullscreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.webkitExitFullscreen) {
        /* Safari */
        document.webkitExitFullscreen();
      } else if (document.msExitFullscreen) {
        /* IE11 */
        document.msExitFullscreen();
      }
    }
  },
  destroyed() {
    clearInterval(this.handle);
  },
  toggleIsClicked() {
      this.$store.commit("toggleDarkmode");
    }
};
</script>

